
.city{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 2px solid #000;
  padding: 25px 0;
}
.city__add{
  position: absolute;
  right: 15px;
  top: 15px;
  font-size: 0;
  border: 2px solid;
  border-radius: 50%;
  background-color: rgba(255, 0, 0, 0);
  width: 55px;
  height: 55px;
}
.city__add::after{
  content: "+";
  display: block;
  font-size: 40px;
  line-height: 45px;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
.city__temp{
  font-size: 2em;
  line-height: 1;
  margin-bottom: 15px;
}
.city__name{
  font-size: 1em;
  line-height: 1;
  margin-bottom: 25px;
}
.city__weather{
  font-size: 1.5em;
}
.saveds{
  padding: 15px 0;
}
.saveds__heading{
  text-align: center;
  font-size: 1.8rem;
  line-height: 1;
}



